.s-events-description {
    max-width: 1456px;
    padding: 118px 0 30px;

    @media (max-width: 1699px) {
        padding: 115px 0 50px;
    }

    @media (max-width: 1199px) {
        padding: 60px 0 87px;
    }

    @media (max-width: 991px) {
        padding: 60px 0 55px;
    }

    @media (max-width: 600px) {
        padding: 30px 0;
    }

    &__wrapper {
        width: 100%;
        max-width: 1036px;
        margin: 0 auto;

        @media (max-width: 1199px) {
            max-width: 762px;
            margin-left: auto;
            margin-right: 0;
        }

        @media (max-width: 991px) {
            max-width: 100%;
        }
    }

    &__title {
        font-size: rem(40);
        line-height: 120%;
        font-weight: 600;
        margin-bottom: rem(40);

        @media (max-width: 1199px) {
            font-size: 32px;
            margin-bottom: 30px;
        }

        @media (max-width: 991px) {
            font-size: 25px;
        }

        @media (max-width: 600px) {
            font-size: 21px;
            margin-bottom: 15px;
        }
    }

    &__text {
        p {
            font-size: 19px;
            line-height: 160%;
            font-weight: 300;
            margin-top: 0;
            margin-bottom: 20px;

            @media (max-width: 1199px) {
                font-size: 17px;
            }

            @media (max-width: 991px) {
                font-size: 15px;
            }

            @media (max-width: 600px) {
                font-size: 13px;
            }
        }
    }
}
