.event-page {
    position: relative;

    .main {
        position: relative;
        // background-color: #000203; // оставляем для дальнейших фонов, что бы не было претензий я придумал как

        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            width: 100%;
            height: 100%;
            background-image: url(".././img/about/bg-page.png");
            background-size: cover;
            background-repeat: no-repeat;
        }
    }
}
