.s-events-last {
    overflow: visible;
    margin-top: rem(30);

    @media (min-width: 768px) {
        margin-top: rem(50);
    }

    @media (min-width: 1024px) {
        margin: rem(62) 0;
    }

    @media (min-width: 1600px) {
        margin: rem(75) 0 0;
    }

    @media (min-width: 1920px) {
        margin: rem(90) 0 0;
    }

    &__wrap {
        display: flex;
        flex-direction: column;
        gap: rem(12);

        @media (min-width: 768px) {
            flex-direction: row;
            gap: rem(20);
        }

        @media (min-width: 1024px) {
            gap: rem(30);
        }
    }

    &__info {
        position: relative;
        padding-top: rem(8);

        @media (min-width: 768px) {
            width: 40%;
        }

        @media (min-width: 1024px) {
            display: flex;
            justify-content: space-between;
            width: 50%;
            padding-top: rem(30);
            flex-direction: row-reverse;
            gap: rem(42);
        }

        &::before {
            position: absolute;
            top: rem(-1);
            left: 0;
            width: 100%;
            height: rem(2);
            background: $black_light;
            content: "";
        }

        &::after {
            position: absolute;
            top: rem(-1);
            left: 0;
            width: 19%;
            height: rem(2);
            background: #f4f4f2;
            transition: all 0.4s ease;
            content: "";
        }

        &:hover {
            &::after {
                width: 50%;
                background: $red;
            }
        }
    }

    &__image {
        margin-left: auto;

        @media (min-width: 768px) {
            width: 60%;
        }

        @media (min-width: 1024px) {
            width: 50%;
        }
    }

    &__date {
        margin-bottom: rem(20);
        font-size: rem(15);
        font-weight: 600;
        line-height: rem(18);
        color: #fff;
        white-space: nowrap;

        @media (min-width: 1440px) {
            font-size: rem(19);
            line-height: rem(23);
        }

        @media (min-width: 1920px) {
            font-size: rem(21);
            line-height: rem(25);
        }
    }

    &__text {
        font-size: rem(15);
        line-height: rem(18);
        color: #656669;

        @media (min-width: 768px) {
            max-width: rem(260);
        }

        @media (min-width: 1440px) {
            max-width: rem(400);
            font-size: rem(19);
            line-height: rem(23);
        }

        @media (min-width: 1920px) {
            max-width: rem(430);
            font-size: rem(21);
            line-height: rem(25);
        }
    }

    &__title {
        margin-bottom: rem(20);
        font-size: rem(35);
        font-weight: 500;
        line-height: rem(35);
        color: #ceced0;
        letter-spacing: -0.03em;

        @media (min-width: 768px) {
            z-index: 1;
            position: absolute;
            bottom: 0;
            left: rem(30);
            max-width: rem(692);
            font-size: rem(40);
            line-height: rem(40);
        }

        @media (min-width: 1024px) {
            bottom: rem(-100);
            left: rem(70);
            font-size: rem(60);
            line-height: rem(70);
        }

        @media (min-width: 1600px) {
            bottom: 0;
            font-size: rem(95);
            max-width: rem(1075);
            line-height: rem(111);
        }

        @media (min-width: 1920px) {
            bottom: rem(-45);
            font-size: rem(110);
            max-width: rem(1200);
            line-height: rem(129);
        }
    }

    &-detail {
        position: relative;
        max-width: 100%;

        @media (min-width: 1600px) {
            max-width: rem(1496);
        }

        .l-default {
            position: relative;
        }

        .s-events-last {
            &__image {
                position: relative;
                width: 100%;
                height: rem(265);

                @media (min-width: 768px) {
                    width: rem(584);
                    height: rem(440);
                }

                @media (min-width: 1024px) {
                    width: rem(611);
                    height: rem(416);
                }

                @media (min-width: 1600px) {
                    width: rem(843);
                    height: rem(635);
                }

                img {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    object-fit: contain;

                    @media (min-width: 768px) {
                        object-fit: cover;
                    }
                }

                &:after {
                    content: '';
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    min-height: 100%;
                    background: linear-gradient(90deg, #000000 -10.56%, rgba(0, 0, 0, 0) 84.99%);
                }
            }

            &__title {
                position: absolute;
                left: 0;
                bottom: rem(-45);
                line-height: 90%;

                @media (min-width: 768px) {
                    bottom: rem(25);
                    font-size: rem(70);
                }

                @media (min-width: 1024px) {
                    bottom: unset;
                    top: 20%;
                    font-size: rem(90);
                }

                @media (min-width: 1600px) {
                    top: 25%;
                    font-size: rem(140);
                }

                @media (min-width: 1920px) {
                    font-size: rem(150);
                }
            }

            &__wrap {
                position: relative;
                padding-top: 12px;

                &::before {
                    position: absolute;
                    top: rem(-1);
                    left: 0;
                    width: 100%;
                    height: rem(2);
                    background: $black_light;
                    content: "";

                    @media (min-width: 768px) {
                        max-width: 585px;
                    }

                    @media (min-width: 1024px) {
                        max-width: 578px;
                    }

                    @media (min-width: 1600px) {
                        max-width: 1131px;
                    }
                }

                &::after {
                    position: absolute;
                    top: rem(-1);
                    left: 0;
                    width: 19%;
                    max-width: 1131px;
                    height: rem(2);
                    background: #f4f4f2;
                    transition: all 0.4s ease;
                    content: "";
                }

                &:hover {
                    &::after {
                        width: 50%;
                        background: $red;
                    }
                }
            }

            &__date {
                max-width: fit-content;
                color: #CECED0;
                margin-top: rem(55);
                margin-left: auto;
                margin-bottom: 0;

                @media (min-width: 768px) {
                    width: rem(584);
                    margin-top: rem(10);
                    margin-left: auto;
                    margin-right: 0;
                }

                @media (min-width: 1024px) {
                    width: 100%;
                    max-width: rem(1066);
                    transform: translateY(-40px);
                    margin: 0 auto;

                    span {
                        display: block;
                    }
                }

                @media (min-width: 1600px) {
                    transform: translateY(-20px);

                    span {
                        display: inline-block;
                    }
                }
            }
        }
    }
}
