.s-events-banner {
    position: relative;
    padding-bottom: 173px;

    @media (max-width: 1699px) {
        padding-bottom: 120px;
    }

    @media (max-width: 1199px) {
        padding-bottom: 94px;
    }

    @media (max-width: 1199px) {
        padding-bottom: 87px;
    }

    @media (max-width: 600px) {
        padding-bottom: 100px;
    }

    &__wrapper {
        position: relative;
        display: flex;
        align-items: flex-end;
        min-height: 635px;
        padding-bottom: rem(50);

        @media (max-width: 1699px) {
            padding-bottom: 80px;
        }

        @media (max-width: 1199px) {
            min-height: 398px;
            padding-bottom: 66px;
        }

        @media (max-width: 991px) {
            min-height: 348px;
            padding-bottom: 75px;
        }

        @media (max-width: 600px) {
            flex-direction: column;
            padding-bottom: 0;
        }
    }

    &__img {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 843px;
        min-height: 100%;

        @media (max-width: 1199px) {
            width: 529px;
        }

        @media (max-width: 991px) {
            width: 462px;
        }

        @media (max-width: 600px) {
            position: relative;
            width: 100%;
            height: 265px;
        }

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            min-height: 100%;
            background: linear-gradient(90deg, #000000 -10.56%, rgba(0, 0, 0, 0) 84.99%);
        }
    }

    &__title {
        max-width: 1100px;
        font-size: 100px;
        line-height: 90%;
        margin-left: auto;

        @media (max-width: 1699px) {
            max-width: 850px;
            font-size: 75px;
        }

        @media (max-width: 1199px) {
            max-width: 441px;
            font-size: rem(40);
        }

        @media (max-width: 991px) {
            max-width: 340px;
            font-size: 30px;
        }

        @media (max-width: 600px) {
            max-width: 100%;
            margin-top: 17px;
        }
    }
}
