.s-events-btn {
    overflow: visible;
    padding-bottom: 172px;

    @media (max-width: 1699px) {
        padding-bottom: 90px;
    }

    @media (max-width: 1199px) {
        padding-bottom: 116px;
    }

    @media (max-width: 991px) {
        padding-bottom: 62px;
    }

    @media (max-width: 600px) {
        padding-bottom: 84px;
    }

    &__wrapper {
        width: 638px;
        margin: 0 auto;

        @media (max-width: 1199px) {
            width: 403px;
        }

        @media (max-width: 600px) {
            width: 100%;
            height: 401px;
        }
    }

    &__container {
        width: 525px;
        height: 566px;
        margin: 0 auto;

        @media (max-width: 1699px) {
            width: 638px;
            height: 683px;
        }

        @media (max-width: 1199px) {
            width: 403px;
            height: 431px;
        }

        @media (max-width: 600px) {
            width: 100%;
            height: 401px;
        }
    }

    &__title {
        font-size: rem(40);
        line-height: 120%;
        color: #4E5056;
        margin-bottom: 35px;

        @media (max-width: 1199px) {
            font-size: 32px;
        }

        @media (max-width: 991px) {
            font-size: 25px;
            margin-bottom: 30px;
        }

        @media (max-width: 600px) {
            font-size: 21px;
            text-align: center;
            margin-bottom: 19px;
        }
    }
}

#blackhole {
	width: 100%;
    height: 100%;
	position: relative;
	display: flex;
    margin: 0 auto;
}

.centerHover {
	width: 255px;
	height: 255px;
	background-color: transparent;
	border-radius: 50%;
	position: absolute;
	left: 50%;
	top: 50%;
	margin-top: -128px;
	margin-left: -128px;
	z-index: 2;
	cursor: pointer;
	line-height: 255px;
	text-align: center;
	transition: all 500ms;

	&.open  {
		opacity: 0;
		pointer-events: none;
	}

	&:hover span {
		color: #DDD;

		&:before { background-color: #DDD; }
		&:after { background-color: #DDD; }
	}

	span {
		color: #fff;
		font-family: serif;
		font-size: 19px;
		position: relative;
		transition: all 500ms;

        @media (max-width: 1199px) {
            font-size: 15px;
        }

		&:before {
			content: '';
			display: inline-block;
			height: 1px;
			width: 16px;
			margin-right: 12px;
			margin-bottom: 4px;
			background-color: #fff;
			transition: all 500ms;
		}
		&:after {
			content: '';
			display: inline-block;
			height: 1px;
			width: 16px;
			margin-left: 12px;
			margin-bottom: 4px;
			background-color: #fff;
			transition: all 500ms;
		}
	}
}

canvas {
	position: relative;
	z-index: 1;
	width: 100%;
	height: 100%;
	margin: auto;
}
